import React, { Component } from 'react'
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider'
import AppBar from 'material-ui/AppBar'
import axios from 'axios'
import { Redirect } from 'react-router-dom'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import '../css/Expense.css'

class ExpenseTable extends Component {
  constructor (props) {
    super(props)
    this.state = {
      categories: []
    }
  }
  componentWillMount () {
    var self = this
    axios
      .get(
        'https://expense-manager-t.herokuapp.com/api/users/' + this.props.userid + '/categories'
      )
      .then(function (response) {
        console.log(response.data)
        self.setState({
          categories: response.data
        })
      })
    console.log(this.state.categories)
  }
  componentWillUpdate () {
    var self = this
    axios
      .get(
        'https://expense-manager-t.herokuapp.com/api/users/' + this.props.userid + '/categories'
      )
      .then(function (response) {
        console.log(response.data)
        self.setState({
          categories: response.data
        })
        self.props.update = false;
      })
    console.log(this.state.categories)
  }

  render () {
    return (
      <div className='table'>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell numeric>Id</TableCell>
              <TableCell>Item</TableCell>
              <TableCell numeric>Price (L)</TableCell>
              <TableCell>Category</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {this.state.categories.map((data, key) => {
              if (data.items != null) {
                return data.items.map(itemData => {
                  return (
                    <TableRow key={data.id}>
                      <TableCell component='th' scope='row' numeric>
                        {itemData.id}
                      </TableCell>
                      <TableCell>{itemData.content}</TableCell>
                      <TableCell numeric>{itemData.price}</TableCell>
                      <TableCell>{data.name}</TableCell>
                    </TableRow>
                  )
                })
              }
            })}
          </TableBody>
        </Table>
      </div>
    )
  }
}
export default ExpenseTable
